.mcKinsey_charts {
  background-color: #f8f8f8;
}

.mcKinsey_charts .header {
  color: #fff;
  position: fixed;
  display: flex;
  /* width: 100%; */
  height: 80px;
  top: 0;
  left: 0;
  padding: 0 1em;
  background-color: #051c2c;
  border-radius: 0 0 30px 30px;
  /* box-shadow: 0 3px 3px rgba(0, 0, 0, 0.1); */
}

.mcKinsey_charts .header h1 {
  margin: auto 0;
}

.mcKinsey_charts section {
  display: flex;
  justify-content: space-between;
  width: 92%;
  margin: 0 auto 0;
  overflow: hidden;
}

.mcKinsey_charts section > div {
  width: 60%;
  height: 100%;
  margin: auto 0;
  padding: 4em 2em;
  background-color: #fff;
  border-bottom: 2px solid #f8f8f8;
  /* box-shadow: -3px 0 3px rgba(0, 0, 0, 0.1); */
  /* border-radius: 15px; */
}

.mcKinsey_charts section header {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 38%;
}

.mcKinsey_charts section header h2 {
  width: 80%;
  font-size: 2.4rem;
  font-family: Arial, Helvetica, sans-serif;
  /* margin: auto 0; */
  line-height: 3rem;
}

.mcKinsey_charts section header h4 {
  color: #00a8f3;
  width: 80%;
  margin-bottom: 1em;
}

.mcKinsey_charts section header h2 a:hover {
  /* text-decoration: underline; */
  color: #00a8f3;
  transition: ease-in 0.5s;
}

@media (max-width: 50em) {
  .mcKinsey_charts section {
    flex-wrap: wrap;
    width: 84%;
    margin: 0 auto;
    height: auto;
  }

  .mcKinsey_charts section header {
    width: 100%;
  }

  .mcKinsey_charts section header h2 {
    margin: 2em 0 0;
  }

  .mcKinsey_charts section > div {
    width: 100%;
    margin: 4em 0;
  }
}
