@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Roboto:ital,wght@0,400;0,500;0,900;1,400&display=swap);
html,
body {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  color: #323742;
  font-size: 1rem;
  margin: 0;
  padding: 0;
  line-height: 1;
  box-sizing: border-box;
  background-color: #fff;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
ul {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #323742;
}

button {
  cursor: pointer;
}

.container {
  width: 92%;
  margin: 0 auto;
}

input:-internal-autofill-selected {
  background-color: yellow !important;
  background-image: none !important;
  color: rgb(0, 0, 0) !important;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
  background-color: #f2f6f3;
  outline: 1px solid slategray;
  border-radius: 10px;
}

:focus {
  outline: none;
}

::-webkit-input-placeholder {
  color: #9dadcf;
}

:-ms-input-placeholder {
  color: #9dadcf;
}

::placeholder {
  color: #9dadcf;
}

.react-spinner div {
  margin: 15% auto;
}

.fashion_industry {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
  font-family: "Times New Roman", Times, serif;
  height: 480px;
  margin: auto 0;
}

.fashion_industry .heading {
  text-align: right;
  width: 80px;
  margin: auto 0;
  margin-left: -100px;
}

.fashion_industry h3 {
  font-size: 2rem;
}

.fashion_industry .heading p {
  font-size: 1.1175rem;
}

.fashion_industry text {
  font-size: 1.1175rem;
}

.fashion_industry line {
  stroke: #d8d8d8;
  stroke-width: 1px;
}

.fashion_industry .yAxisGroup .tick:nth-child(4) line,
.fashion_industry .yAxisGroup .tick:nth-child(2) line {
  display: none;
}

.fashion_industry .yAxisGroup path,
.fashion_industry .xAxisGroup path {
  display: none;
}

/* .fashion_industry rect:hover {
  fill: #00a8f3;
  transition: ease-in 0.3s;
} */

.fashion_industry svg {
  width: 84%;
}

.labor_productivity text {
  font-size: 1rem;
}

.labor_productivity .yAxisGroup line,
.labor_productivity .xAxisGroup line {
  stroke: #d8d8d8;
}

.labor_productivity .yAxisGroup path,
.labor_productivity .xAxisGroup path {
  display: none;
}

.labor_productivity rect {
  fill: rgba(0, 0, 0, 0.1);
}

.latin_america .product {
  position: relative;
}

.latin_america .yAxisLeftGroup line,
.latin_america .xAxiGroup line,
.latin_america .xAxisGroup line,
.latin_america .yAxisRightGroup line {
  stroke: #d8d8d8;
}

.latin_america .xAxisGroup path,
.latin_america .yAxisLeftGroup path,
.latin_america .xAxisProfitGroup path,
.latin_america .yAxisRightGroup path {
  display: none;
}

.latin_america .labels {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 74%;
  margin: 0 auto;
  margin-bottom: 1em;
}

.latin_america .labels h4 {
  text-align: center;
  width: 190px;
}

.latin_america .labels h4:last-of-type {
  width: 150px;
}

.life-expectation {
  font-family: Arial, Helvetica, sans-serif;
}

.life-expectation h3 {
  font-size: 1.25rem;
  line-height: 1.5rem;
}

.life-expectation h4 {
  font-size: 1rem;
  margin: 1em 0;
}

.life-expectation h4 span:last-of-type {
  font-weight: normal;
}

.life-expectation .yAxisGridGroup line,
.life-expectation .xAxisGridGroup line,
.life-expectation .xAxisGroup line {
  stroke: #d8d8d8;
}

.life-expectation svg text,
.life-expectation svg text {
  font-size: 1rem;
}

.life-expectation .yAxisGroup path,
.life-expectation .yAxisGridGroup path,
.life-expectation .xAxisGridGroup path,
.life-expectation .yAxisRightGroup path,
.life-expectation .yAxisRightGroup line {
  display: none;
}

.life-expectation .lineGroup {
  stroke-width: 3;
}

.life-expectation .life_exp {
  width: 82%;
  margin: 0 auto;
  margin-bottom: 3em;
}

.life-expectation .population {
  width: 82%;
  margin: 0 auto;
}

.life-expectation .tooltip span {
  display: block;
  margin-top: 0.5em;
}

@media (max-width: 50em) {
  .life-expectation .life_exp {
    width: 100%;
  }

  .life-expectation .population {
    width: 100%;
  }
}

.women_workplace line,
.women_workplace .tick line,
.women_workplace .xAxisGridGroup path {
  display: inline-block;
  stroke: #d8d8d8;
}

.women_workplace .yAxisGroup .tick:first-of-type line {
  display: none;
}

.women_workplace text {
  font-size: 0.875rem;
}

.women_workplace path {
  display: none;
}

.women_workplace .labels ul {
  font-size: 0.875rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 86%;
  margin: 0 auto 1em;
}

.women_workplace .labels ul li span:first-of-type {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: auto 0.5em;
  border-radius: 50%;
}

.beyond_covid .series text {
  fill: white;
  font-size: 1rem;
  font-weight: 500;
}

.beyond_covid .xAxistTextGroup text {
  font-weight: 500;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: 2rem;
  fill: black;
}

.beyond_covid .labelTextGroup div {
  line-height: 1rem;
  white-space: pre-wrap;
  white-space: -moz-pre-wrap; /* Mozilla, since 1999 */
  white-space: -pre-wrap; /* Opera 4-6 */
  white-space: -o-pre-wrap; /* Opera 7 */
  word-wrap: break-word; /* Internet Explorer 5.5+ */
}

.beyond_covid .net text tspan:first-of-type {
  font-weight: 600;
}

.covid_exit .xAxisGroup line,
.covid_exit .xAxisGroup path,
.covid_exit .yAxisGroup line,
.covid_exit .yAxisGroup path,
.covid_exit .xAxisGridGroup path {
  display: none;
}

.covid_exit .xAxisGridGroup line,
.covid_exit .polylineGroup polyline {
  stroke: #d8d8d8;
}

.covid_exit .xAxisGroup text,
.covid_exit .yAxisGroup text {
  font-size: 0.875rem;
}

.covid_exit .polylineTextGroup {
  font-size: 0.875rem;
}

.covid_exit .stackedGroup .series:first-of-type path {
  fill: #e5e7e9;
}

.covid_exit .stackedGroup .series path {
  fill: white;
}

.mcKinsey_charts {
  background-color: #f8f8f8;
}

.mcKinsey_charts .header {
  color: #fff;
  position: fixed;
  display: -webkit-flex;
  display: flex;
  /* width: 100%; */
  height: 80px;
  top: 0;
  left: 0;
  padding: 0 1em;
  background-color: #051c2c;
  border-radius: 0 0 30px 30px;
  /* box-shadow: 0 3px 3px rgba(0, 0, 0, 0.1); */
}

.mcKinsey_charts .header h1 {
  margin: auto 0;
}

.mcKinsey_charts section {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 92%;
  margin: 0 auto 0;
  overflow: hidden;
}

.mcKinsey_charts section > div {
  width: 60%;
  height: 100%;
  margin: auto 0;
  padding: 4em 2em;
  background-color: #fff;
  border-bottom: 2px solid #f8f8f8;
  /* box-shadow: -3px 0 3px rgba(0, 0, 0, 0.1); */
  /* border-radius: 15px; */
}

.mcKinsey_charts section header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 38%;
}

.mcKinsey_charts section header h2 {
  width: 80%;
  font-size: 2.4rem;
  font-family: Arial, Helvetica, sans-serif;
  /* margin: auto 0; */
  line-height: 3rem;
}

.mcKinsey_charts section header h4 {
  color: #00a8f3;
  width: 80%;
  margin-bottom: 1em;
}

.mcKinsey_charts section header h2 a:hover {
  /* text-decoration: underline; */
  color: #00a8f3;
  transition: ease-in 0.5s;
}

@media (max-width: 50em) {
  .mcKinsey_charts section {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 84%;
    margin: 0 auto;
    height: auto;
  }

  .mcKinsey_charts section header {
    width: 100%;
  }

  .mcKinsey_charts section header h2 {
    margin: 2em 0 0;
  }

  .mcKinsey_charts section > div {
    width: 100%;
    margin: 4em 0;
  }
}

.vietnam_education .xAxisGroup path,
.vietnam_education .xAxisGroup line,
.vietnam_education .yAxisGroup path {
  stroke: #d8d8d8;
}

.vietnam_education svg text {
  font-size: 0.5rem;
}

.vietnam_education .tooltip span {
  display: block;
  margin-bottom: 0.5em;
}

